@import 'https://use.fontawesome.com/releases/v5.5.0/css/all.css';
@import 'https://fonts.googleapis.com/css?family=Roboto';
:root {
  --default-font-size: 1rem;
  --mobile-font-size: 0.875rem;
  --site-padding: 1rem;
  --text-main-color: #dadada;
  --link-color: #49A0CC;
  --nav-blue: #2693E6;
  --default-box-shadow: 0px 0px 22px 2px rgba(0, 0, 0, 0.4); }

* {
  margin: 0;
  padding: 0; }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*,
*::before,
* ::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

#__bs_notify__ {
  display: none !important; }

html {
  font-family: 'Roboto', sans-serif;
  line-height: 1.5;
  font-size: var(--mobile-font-size); }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--default-font-size); } }

body, table, th, td, form, input, textarea, option, select {
  font-family: inherit; }

a {
  color: var(--link-color);
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

p, ul, ol {
  padding-bottom: .5rem; }

ul, ol {
  padding-left: 2rem; }

li {
  list-style-position: inside; }

header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-bottom: 1px solid gray;
  margin-bottom: 1.5rem;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.site-title {
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold; }

.site-title a {
  color: var(--nav-blue); }

.site-title a:hover {
  text-decoration: none;
  color: #dadada; }

header ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 0;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

header > nav > ul > li:not(:last-child) {
  padding-right: .7rem; }

header li {
  list-style: none;
  text-transform: capitalize;
  font-size: 1.2rem; }

header ul a {
  color: white;
  padding: .5rem 1rem;
  background: var(--nav-blue);
  border-radius: 3px;
  display: block; }

header ul a:hover {
  text-decoration: none;
  background: white;
  color: #2693E6;
  -webkit-box-shadow: var(--default-box-shadow);
  box-shadow: var(--default-box-shadow); }

.far, .fas {
  margin-right: 5px;
  font-size: 1.1rem; }

#nav-toggle, #toggle {
  display: none; }

@media screen and (max-width: 800px) {
  #toggle {
    display: block;
    cursor: pointer; }
  nav {
    display: none; }
  nav > ul {
    display: block; }
  #nav-toggle:checked + header nav {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 100%;
    flex: 1 0 100%; }
  header > nav > ul > li:not(:last-child) {
    padding-bottom: .8rem; }
  header {
    padding-bottom: .6rem; }
  header > nav > ul > li {
    padding-right: .7rem; }
  #nav-toggle:checked + header .fa-bars:before {
    content: '\f00d'; } }

.gallery {
  text-align: center; }

.gallery h1 {
  font-size: 2rem; }

.gallery-images img {
  width: 30%;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  /* Animation */
  margin: .4rem; }

.gallery-images img:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  -webkit-box-shadow: var(--default-box-shadow);
  box-shadow: var(--default-box-shadow); }

body {
  background: #111111;
  color: var(--text-main-color);
  max-width: 65rem;
  margin: 0 auto; }

.site {
  background: #222222;
  padding: 0.7rem 2rem; }
